:root {
  --font-inter: "Inter", sans-serif;
}

:root {
  --black: hsl(0, 0%, 11%);
  --ltblack: hsl(0, 0%, 21%);
  --white: hsl(0, 0%, 100%);
  --dkwhite: hsl(0, 8%, 71%);
  --gray: hsl(0, 0%, 49%);
  --yellow: hsl(60, 100%, 50%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  min-height: 150vh;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.header {
  display: flex;
  justify-content: center;
  margin-top: 0.9375rem;
}
.header__button {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 210px;
  padding: 0.375rem;
  border: 0 solid;
  background-color: var(--gray);
  border-radius: 1.5rem;
  opacity: 0.85;
}
.header__button a {
  background-color: var(--black);
  color: var(--white);
  padding: 6px 26px;
  margin: 0.0625rem 0.06875rem;
  cursor: pointer;
  border-radius: 1rem;
  width: 6.25rem;
  height: 2.1875rem;
  box-shadow: 0 0.625rem 1.5625rem rgba(0, 0, 0, 0.5);
  transition: opacity 200ms ease-in-out;
}
.header__button a:hover {
  background-color: var(--ltblack);
}
.header__button:hover {
  opacity: 0.82;
}